  //   //   .required("Telefone fixo  um campo obrigatrio"),
  //   // celular_pj: yup.string().required("Celular CNPJ  um campo obrigatrio"),
  //   // telefone_fixo: yup.string().required("Telefone fixo  um campo obrigatrio"),
  //   nome_fantasia: yup
  //     .string()
  //     .required("Nome fantasia  um campo obrigatrio"),
  //   // site: yup.string().required("Site  um campo obrigatrio"),
  // }),

  // endereco_cnpj: yup.object({
  //   endereco: yup.string().required("Endereo  um campo obrigatrio"),
  //   cep: yup.string().required("CEP  um campo obrigatrio"),
  //   complemento: yup.string(),
  //   bairro: yup.string().required("Bairro  um campo obrigatrio"),
  //   numero: yup.string().required("Numero  um campo obrigatrio"),
