import * as yup from "yup";

const validationSchema = yup.object({
  empresa: yup.object({
    cnpj: yup.string().required("CNPJ  um campo obrigatrio"),
    cnae: yup.string().required("CNAE  um campo obrigatrio"),
    razao_social: yup.string().required("Razo social  um campo obrigatrio"),
    celular: yup.string().required("Celular  um campo obrigatrio"),
    telefone_fixo: yup
      .string()
      .required("Telefone fixo  um campo obrigatrio"),
    nome_fantasia: yup
      .string()
      .required("Nome fantasia  um campo obrigatrio"),
    site: yup
      .string()
      .required("Campo Site  obrigatrio")
      .url("Insira um site valido ex: 'https://www.google.com'"),
  }),

  endereco_cnpj: yup.object({
    endereco: yup.string().required("Endereo  um campo obrigatrio"),
    cep: yup.string().required("CEP  um campo obrigatrio"),
    complemento: yup.string(),
    bairro: yup.string().required("Bairro  um campo obrigatrio"),
    numero: yup.string().required("Numero  um campo obrigatrio"),
    cidade: yup.string().required("Cidade  um campo obrigatrio"),
    estado: yup.string().required("Estado  um campo obrigatrio"),
  }),
});

export default validationSchema;
